import React from "react";
import OwlCarousel from "react-owl-carousel";
import $ from "jquery";
import data from "./data";

function Testimonial() {
  const options = {
    margin: 10,
    responsiveClass: true,
    autoWidth: true,
    nav: true,
    rewindNav: true,
    dots: true,
    smartSpeed: 1000,
    responsive: {
      0: {
        items: 1,
      },
      400: {
        items: 1,
      },
      600: {
        items: 1,
      },
      700: {
        items: 2,
      },
      1000: {
        items: 3,
      },
    },
  };

  return (
    <OwlCarousel className="owl-theme" loop margin={10} {...options}>
      {data.map((data) => (
        <div className="item">
          <div className="testimonial-item">
            <div className="item-paragraph-section">
              <p className="item-paragraph">{data.text}</p>
            </div>
            <div className="item-title-section">
              <div className="item-image-section">
                <img className="item-image" src={data.avatar} alt="" />
              </div>
              <div className="item-image-title-section">
                <p className="item-image-title">{data.name}</p>
                <p className="item-image-profession">
                  {data.designation} at {data.company}
                </p>
              </div>
            </div>
          </div>
        </div>
      ))}
    </OwlCarousel>
  );
}

export default Testimonial;

$("#featuredproducts .owl-nav").click(function (event) {
  $(this).removeClass("disabled");
});
